/* Layout */
.container {
  max-width: 960px;
  margin: 24px auto;
  padding: 16px;
  font-family: ui-sans-serif, system-ui, Arial;
}
.headerRow {
  display: flex;
  flex-wrap: wrap;
  gap: 12px;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 12px;
}
.title {
  font-size: 28px;
  font-weight: 700;
  margin: 0;
}
.hint {
  margin: 4px 0 0;
  color: #6b7280;
  font-size: 12px;
}
.toolbar {
  display: flex;
  flex-wrap: wrap;
  gap: 8px;
  align-items: center;
}
.select {
  padding: 8px 10px;
  border: 1px solid #d1d5db;
  border-radius: 10px;
  background: #fff;
}
.button {
  padding: 8px 10px;
  border: 1px solid #d1d5db;
  background: #fff;
  border-radius: 10px;
  cursor: pointer;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}
.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}
.buttonActive {
  background: #fde68a;
  border-color: #f59e0b;
}
.primaryBtn {
  padding: 8px 12px;
  border: 0;
  background: #4f46e5;
  color: #fff;
  border-radius: 12px;
  box-shadow: 0 2px 6px rgba(79, 70, 229, 0.35);
  cursor: pointer;
}
.primaryBtn:hover {
  filter: brightness(1.05);
}
.timer {
  padding: 8px 12px;
  border-radius: 10px;
  background: #f3f4f6;
  border: 1px solid #e5e7eb;
  font-family: ui-monospace, SFMono-Regular, Menlo, Consolas, monospace;
}

.gameRow {
  display: flex;
  gap: 24px;
  align-items: flex-start;
  flex-wrap: wrap;
}
.sidePanel {
  min-width: 240px;
  flex: 1;
}

/* Board */
.board {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(9, 1fr);
  gap: 0;
  background: #e5e7eb;
  padding: 6px;
  border-radius: 16px;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.08);
}
.cell {
  position: relative;
  height: 56px;
  width: 56px;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #fff;
  border: 1px solid #c7cdd6;
  cursor: pointer;
  user-select: none;
  font-size: 20px;
}
/* 3x3 borders */
.cell {
  border-top-width: 1px;
  border-left-width: 1px;
}
.cell:nth-child(9n + 1) {
  border-left-width: 2px;
}
.cell:nth-child(-n + 9) {
  border-top-width: 2px;
}
/* thicker borders every 3 cells horizontally */
.board > :nth-child(3n) {
  border-right-width: 2px;
}

/* states */
.cellFixed {
  background: #f3f4f6;
  font-weight: 600;
}
.cellSelected {
  outline: 2px solid #6366f1;
  z-index: 1;
}
.cellPeer {
  background: #eef2ff;
}
.cellSame {
  background: #fff7ed;
}
.cellConflict {
  color: #dc2626;
}
.valFixed {
  color: #111827;
}
.valUser {
  color: #4338ca;
}
.placeholder {
  color: transparent;
}

/* Notes */
.notesGrid {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(3, 1fr);
  gap: 2px;
  width: 100%;
  height: 100%;
  padding: 6px;
  box-sizing: border-box;
}
.note {
  font-size: 10px;
  line-height: 1;
  text-align: center;
  color: #6b7280;
}

/* Numpad */
.numpad {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 8px;
}
.numpadBtn {
  padding: 12px;
  border: 1px solid #d1d5db;
  background: #fff;
  border-radius: 12px;
  font-size: 16px;
  cursor: pointer;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}
.wide {
  grid-column: span 3;
}

/* Legend & Status */
.legend {
  margin-top: 16px;
  display: grid;
  gap: 8px;
  color: #374151;
  font-size: 14px;
}
.legendSwatch {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 16px;
  height: 16px;
  border-radius: 4px;
  border: 1px solid #d1d5db;
  margin-right: 8px;
}
.swatchGiven {
  background: #f3f4f6;
}
.swatchSame {
  background: #fff7ed;
}
.swatchPeer {
  background: #eef2ff;
}
.swatchConflict {
  border-color: #ef4444;
  color: #ef4444;
  font-size: 12px;
}

.statusBox {
  margin-top: 16px;
  padding: 12px;
  border: 1px solid #e5e7eb;
  background: #f9fafb;
  border-radius: 12px;
}
.statusRow {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.solved {
  color: #15803d;
  font-weight: 700;
}
.filledWrong {
  color: #b45309;
  font-weight: 700;
}
.inProgress {
  color: #111827;
  font-weight: 700;
}

.footerNote {
  margin-top: 12px;
  color: #6b7280;
  font-size: 12px;
}

/* Responsive */
@media (max-width: 640px) {
  .board {
    padding: 4px;
  }
  .cell {
    height: 44px;
    width: 44px;
    font-size: 18px;
  }
}
